* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

html {
    height: 100%;
}

body{
  height: 100%;
}

.page{
  height: 100%;
  width: 100%;
}

.chessboard{
  position: fixed;
  height: 700px;
  width: 700px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  border: 2px solid #666;
  z-index: 9;
  cursor: pointer;
  background-color: #eee;
}

.row{
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: #666666;
  z-index: 9;
}

.col{
  position: absolute;
  width: 2px;
  height: 100%;
  top: 0;
  background-color: #666666;
  z-index: 9;
}

.center-point{
  position: absolute;
  background-color: #f00;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  z-index: 10;
}

.chess{
  display: none;
  position: absolute;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  -webkit-box-shadow: 0 0 5px 2px rgba(0,0,0,.1);
          box-shadow: 0 0 5px 2px rgba(0,0,0,.1);
  z-index: 99;
}

.chess.white{
  display: block;
  background-color: #fff;
}

.chess.black{
  display: block;
  background: radial-gradient(#999,#666,#333,#000);
}

.chess-pointer{
  position: absolute;
  width: 40px;
  height: 40px;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  z-index: 99;
}

.cp{
  position: absolute;
  width: 5px;
  height: 5px;
}

.cp-l{
  border-left: 1px solid #f00;
  left: 0;
}

.cp-r{
  border-right: 1px solid #f00;
  right: 0;
}

.cp-t{
  border-top: 1px solid #f00;
  top: 0;
}

.cp-b{
  border-bottom: 1px solid #f00;
  bottom: 0;
}

.btn-blue{
  background-color: #408cff;
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  outline: none;
  border-radius: 5px;
  margin-top: 50px;
  cursor: pointer;
}
